body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  background: #222830;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.list__header {
  color: #f4841a;
}

.list {
  width: 50vw;
  background: #b0b5b5;
  box-shadow:          0 0 10px #000000;
  padding: 1.5rem 3rem;
  border-radius: 2px;
  margin: 0;
}

.item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
}

.item__body {
  cursor: pointer;
  margin-bottom: 0.8rem;
  transition: all .3s ease-out;
}
.item__body:first-letter {
  text-transform: uppercase;
}
.item__body:hover {
  list-style: none;
  padding-left: 1.5rem;
  color: #cc6404;
}

.item__btn-delete {
  font-size: 1rem;
  margin-left: 0.5rem;
  outline: none;
  border: none;
  background: rgba(255, 255, 255, 0);
  transition: all .3s ease-out;
}
.item__btn-delete:hover {
  color: #c6091c;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.article__container {
  width: 50vw;
  background: #b0b5b5;
  box-shadow:          0 0 10px #000000;
  padding: 1.5rem 3rem;
  border-radius: 2px;
}

.article__header {
  text-transform: uppercase;
  text-align: center;
  color: #004c56;
}

.article__body:first-letter {
  text-transform: uppercase;
}

.form  {
  box-shadow:          0 0 10px #000000;
  background: rgba(176, 181, 181, 0.2);
  padding: 1.5rem 3rem;
  border-radius: 2px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  color: rgb(244, 132, 26);
}

.form__btn, .form__input{
  border: 1px solid #222830;
  border-radius: 0.2rem;
  margin-bottom: 0.5rem;
  outline: none;
  height: 1.7rem;
}

.form__input{
  text-align: center;
}

.form__btn {
  cursor: pointer;
  margin-top: 1rem;
  width: 5rem;
  background: rgba(244, 132, 26, 0.2);
  transition: all .3s ease-out;
}
.form__btn:hover {
  background: rgb(244, 132, 26);
	 box-shadow: 0 8px 6px -6px black;
}

.wrongUsername {
  display: none;
}

